.cookie-font{
    font-family: 'Cookie', cursive;
    font-size: 80px;
    margin: 0 0 0 0;
  }
  
  .main-title{
    font-size: 120px;
    color: white;
    font-family: 'Cookie', bold;
  }
  
  .paint-pink{
    color: #ED1D5A;
  }
  
  .landing-title{
    margin-top: 20%;
    color: #ED1D5A;
  }
  
  .landing-subtitle{
    // font-family: ;
    font-size: 20px;
    margin: 0 0 0 0;
    color: rgb(231, 244, 96)
  }

  .cardscroll {
    display: grid;
    overflow: scroll;
    width: 100%;
    height: 800px;

}

.no-margin-vert{
    margin-top: 0;
    margin-bottom: 0;
}

.no-padding-top{
    padding-top: 0;
}

.column-3 {
    float: left;
    margin: 0;
  }
  
.right, .middle {
    width: 40%
  }
  
.left {
    width: 20%;
  }
  
  
  /* Clear floats after the columns */
.row:after {
    content: "";
    display: table-column;
    clear: both;
  }

.row-height{
    height: 800px
}

.index-subtitle{
    padding-left: 20px;
    font-size: 30px;
    color: white
}
  